import Main from "./Main";
import News from "./News";
import Details from "./Details";

const routes = [
  {
    path: "/",
    exact: true,
    component: Main
  },
  {
    path: "/details/:id",
    exact:true,
    component: Details
  },
  {
    path: "/news",
    component: News
  }
];

export default routes;